let demo = document.querySelector('#demo');
let string = `
你好，我是一名前端新人
接下来我要开始加样式了
我要加的样式是
body{
    color: red;
}
`;
let string2 = "";
let n = -1;

demo.innerHTML = string.substring(0, n);

let step = ()=>{
    setTimeout(()=>{
        n = n + 1;
        if(string[n] === "\n"){
            string2 += "<br>";
        }else{
            string2 += string[n];
        }
        demo.innerHTML = string2;
        if (n < string.length){
            step();
        }  
    },100)
    
};

step();
